.main-paralax{
    height: 500px;
    background:
            linear-gradient(rgba(#131313, 0.64),rgba(#444, .64)),
            url("../images/banner-paralax.jpg");
    background-size: cover;
    background-attachment: fixed;
}
.main-paralax .container{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.main-paralax-title{
    font-family: "Poiret One", sans-serif;
    font-size: 60px;
    font-weight: normal;
    color: $color-main;
    text-transform: uppercase;
}
.main-paralax-title span{
    color: #fff;
}
.main-paralax-text{
    max-width: 840px;
    font-size: 19px;
    color: rgba(#fff, .35);
    text-align: center;
}